import React from 'react'
import "../sass/main.scss"
import "../sass/games.scss"

import Layout from '../layouts/main'

class Post extends React.Component {
    render() {
        return  <Layout>
                    <p><b>This section is in progress.</b></p>
                    <p>I'm currently cleaning up some of my old gamedev projects and porting them to itch.io. Keep an eye on this page as this is where I'll be listing them.</p>

                    <div class="game">
                        <img src="static/PuzzleDungeon.png" height="200" width="200"></img>
                        <div class="game-details">
                            <div class="game-title">
                                <a href="https://killianmccabe.github.io/PuzzleDungeon/" target="_blank">Puzzle Dungeon</a>
                                <i class="game-platform" title="HTML5 (web)" class="fab fa-html5"></i>
                            </div>
                            <div class="game-categories">
                                <div class="game-category">2D</div>
                                <div class="game-category">Puzzle</div>
                            </div>
                            <div class="game-description">
                                <p>Explore a dungeon filled with puzzles. Each puzzle is harder than the ones before. Can you reach the end?</p>
                            </div>
                        </div>
                    </div>

                    <div class="game">
                        <img src="static/Memory.png" height="200" width="200"></img>
                        <div class="game-details">
                            <div class="game-title">
                                <a href="https://killianmccabe.github.io/MemoryGame/" target="_blank">Memory</a>
                                <i class="game-platform" title="HTML5 (web)" class="fab fa-html5"></i>
                            </div>
                            <div class="game-categories">
                                <div class="game-category">Puzzle</div>
                            </div>
                            <div class="game-description">
                                <p>Train your brain by memorising cards and matching pairs.</p>
                            </div>
                        </div>
                    </div>
                </Layout>
    }
}

export default Post